import axios from '../api/axios'
import requests from '../api/requests'
import React, { useEffect, useState } from 'react'
import styled from 'styled-components'

const Banner = () => {
  const [movie, setMovie] = useState([])
  const [scroll, setScroll] = useState(false)
  const [isClicked, setIsClicked] = useState(false)

  useEffect(() => {
    fetchData()
    window.addEventListener('scroll', () => {
      if (window.scrollY > 100) {
        //setScroll(true)
      } else {
        //setScroll(false)
      }
    })
    return () => {
      window.removeEventListener('scroll', () => {})
    }
  }, [])

  const fetchData = async () => {
    // 현재 상영중인 영화 정보를 가져오기
    const request = await axios.get(requests.fetchNowPlaying)
    // 여러 영화 중 영화 하나의 id를 가져오기
    const movieId =
      request.data.results[
        Math.floor(Math.random() * request.data.results.length)
      ].id
    //console.log(request)
    // 특정 영화의 더 상세한 정보를 가져오기 (비디오 정보 포함)
    const { data: movieDetail } = await axios.get(`movie/${movieId}`, {
      params: { append_to_response: 'videos' },
    })
    console.log(movieDetail) // 특정 하나의 영화의 모든 정보가 담겨있음.
    setMovie(movieDetail)
  }
  const truncate = (str, n) => {
    return str?.length > n ? str.substr(0, n - 1) + '...' : str
  }

  if (!isClicked) {
    return (
      <div className={`banner_container ${scroll && 'isScrolled'}`}>
        <div
          className={`banner`}
          style={{
            backgroundImage: `url("https://images.tmdb.org/t/p/original/${movie?.backdrop_path}")`,
            backgroundPosition: 'top center',
            backgroundSize: 'cover',
          }}
        >
          <div className="banner_contents">
            <h1 className="banner_title">
              {movie?.title}
              {/* {movie?.title || movie?.name || movie?.original_name} */}
            </h1>
            <div className="banner_buttons">
              {movie.videos?.results?.length > 0 &&
                movie.videos.results[0].key && (
                  <button
                    className="banner_button play"
                    onClick={() => setIsClicked(true)}
                  >
                    play
                  </button>
                )}
              <button className="banner_button info">
                <div className="space"></div> more information
              </button>
            </div>

            <h1 className="banner_description">
              {truncate(movie?.overview, 100)}
            </h1>
          </div>
          <div className="banner--fadeBottom" />
        </div>
        <div className="banner--fadeTop" />
      </div>
    )
  } else {
    return (
      <Container className={`banner_container ${scroll && 'isScrolled'}`}>
        <HomeContainer>
          <Iframe
            src={`https://www.youtube.com/embed/${movie.videos.results[0].key}
            ?controls=0&autoplay=1&loop=1&mute=1&playlist=${movie.videos.results[0].key}`}
            // width='640'
            // height="360"
            frameBorder="0"
            allow="autoplay; fullscreen"
          ></Iframe>
        </HomeContainer>
        <div className="banner--fadeTop" ></div>
      </Container>
    )
  }
}

const Container = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100vh;
`
const HomeContainer = styled.div`
  width: 100%;
  height: 100%;
`
const Iframe = styled.iframe`
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 1;
  border: none;

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
`

export default Banner
